import { Component, OnInit, ViewChild } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AnuncioAppService } from '../../../../commons/services/app-terrae-services/anuncio.service';
import { AnuncioAppModel } from 'src/app/commons/models/app-terrae/anuncios/anuncio.model';
import { UtilAlert } from '../../../../commons/util/util.alert';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { Router } from '@angular/router';
import * as moment from 'moment';
import { UsuarioModel } from '../../../../commons/models/aerc-models/usuario-model';
import { FiltroAlumnoApp } from '../../../../commons/interfaces/app-terrae/filtro-alumno.interface';
import { UsuarioApp } from '../../../../commons/interfaces/app-terrae/usuario-app.interface';
import { MatPaginator, MatPaginatorIntl, PageEvent } from '@angular/material/paginator';
import { MatTableDataSource } from '@angular/material/table';
import { timeStamp } from 'console';
import { HttpClient } from '@angular/common/http';
import { Util } from '../../../../commons/util/util';
import { MatSelect } from '@angular/material/select';
import { MatOption } from '@angular/material/core';
import { TipoAnuncioAppModel } from 'src/app/commons/models/app-terrae/anuncios/tipo-anuncio.model';
import { FiltroAnuncioModel } from 'src/app/commons/models/app-terrae/anuncios/filtro-anuncio.model';
import { ConfigAnuncioAppModel } from 'src/app/commons/models/app-terrae/anuncios/config-anuncio.model';

@Component({
  selector: 'app-index-anuncios-new',
  templateUrl: './index-anuncios-new.component.html',
  styleUrls: [ './index-anuncios-new.component.css']
})
export class IndexAnunciosNewComponent implements OnInit {

  public namePage = "Nuevo Anuncio"

  public formAnuncio: FormGroup;

  public tipoAnuncio: number = 1;
  public isSeleccion: boolean = false;
  public listadoTipoAnuncio: TipoAnuncioAppModel[] = [];

  //* FILTROS
  public listadoTipoFiltro: FiltroAnuncioModel[] = [];
  public listadoFiltroTipoAlumno = [];
  public listadoFiltroPrograma = [];
  public listadoFiltroStatus = [];
  public listadoFiltroEscFac = [];

  public isProgramada: boolean = false;
  public isPushNotification: boolean = false;

  public configAnuncio = new ConfigAnuncioAppModel();
  public tipoAnuncioTemp = new TipoAnuncioAppModel();
  public anuncioTemp = new AnuncioAppModel();

  public imagenBase64: string = "";
  public withImage: boolean = true;
  public withLink: boolean = true;

  public enviarConSeleccion: boolean = false;

  //* LISTADO ALUMNOS SELECCION
  //* PAGINACION
  public listadoAlumnos: UsuarioApp[] = [];
  public listadoAlumnosFiltrado: UsuarioApp[] = [];

  public displayedColumns: string[] = ['username', 'nombre', 'emailInstitucional', 'agregar'];
  public listadoPaginator = new MatTableDataSource;
  public listadoFiltradoPaginator = new MatTableDataSource;;

  public formTopBar: FormGroup;
  public pageSize: number = 10;
  public page: number = 1;
  public totalElements: number = 0;
  public filtros = new FiltroAlumnoApp();

  public tipoUsuario: string = "ALUMNO";
  public programaCod: string = "TODOS";
  public tipoPrograma: string = "";
  public statusCode: string[] = ['TODOS'];
  public escFacCod: string[] = ['TODOS'];
  public tipoAlumno: string[] = ['TODOS'];
  public idBanner = "TODOS";

  public preSelect = ['TODOS'];

  public timer: any;
  @BlockUI('window') blockUI: NgBlockUI;

  @ViewChild('paginator') paginator: MatPaginator;
  @ViewChild('paginatorFiltrado') paginatorFiltrado: MatPaginator;

  constructor(
    private anuncioService: AnuncioAppService,
    private utilAlert: UtilAlert,
    private router: Router,
    private paginatorIntl: MatPaginatorIntl,
    private http: HttpClient,
  ) { }


  ngOnInit(): void {
    this._initFormAnuncio();
    this._listarTipoAnuncio();
    this.crearTopBar();

    this.paginatorIntl.firstPageLabel = 'Primera página';
    this.paginatorIntl.nextPageLabel = 'Siguiente página';
    this.paginatorIntl.previousPageLabel = 'Página anterior';
    this.paginatorIntl.lastPageLabel = 'Última página';
    this.paginatorIntl.itemsPerPageLabel = 'Registros por página';
  }

  //** SERVICIOS DE INICIACION */

  private _initFormAnuncio(): void {
    this.formAnuncio = new FormGroup({
      titulo: new FormControl("", [Validators.required]),
      mensaje: new FormControl("", [Validators.required]),
      remitente: new FormControl("", [Validators.required]),
      // linkTitulo: new FormControl(""),
      link: new FormControl(""),
      fechaPublicacion: new FormControl(""),
      tipoAnuncio: new FormControl("", [Validators.required])
    })
  }

  crearTopBar() {
    this.formTopBar = new FormGroup({
      tipoAlumno: new FormControl(this.preSelect),
      programa: new FormControl(''),
      status: new FormControl(this.preSelect),
      escuelaFacultad: new FormControl(this.preSelect),
      registros: new FormControl('')
    });

    this.formTopBar.get("tipoAlumno").setValue(this.tipoAlumno);
    this.formTopBar.get("programa").setValue(this.programaCod);
    this.formTopBar.get("status").setValue(this.statusCode);
    this.formTopBar.get("escuelaFacultad").setValue(this.escFacCod);
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  private _initFiltros() {
    this.listadoFiltroTipoAlumno = [ ... this.listadoTipoFiltro.filter(f => f.tipo == "tipo_alumno")[0].filtros ];
    this.listadoFiltroPrograma = [ ... this.listadoTipoFiltro.filter(f => f.tipo == "programa")[0].filtros ];
    this.listadoFiltroStatus = [ ... this.listadoTipoFiltro.filter(f => f.tipo == "status")[0].filtros ];
    this.listadoFiltroEscFac = [ ... this.listadoTipoFiltro.filter(f => f.tipo == "escuela_facultad")[0].filtros ];

    this.listadoFiltroStatus.splice(0,1);
    this.listadoFiltroTipoAlumno.splice(0,1);
  }

  private _setFiltros() {
    this.filtros.tipoUsuario = this.tipoUsuario.toString();
    this.filtros.programaCod = this.programaCod.toString();
    this.filtros.statusCode = this.statusCode.toString();
    this.filtros.escFacCod = this.escFacCod.toString();
    this.filtros.tipoAlumno = this.tipoAlumno.toString();
    this.filtros.idBanner = this.idBanner;
    console.log(this.filtros);

    if (this.paginator != undefined) {

      this.paginator.firstPage();
      this.page = 1;
      this.pageSize = 10;
    }
  }


  //* SERVICIOS PARA LISTAR INFORMACION */

  public _listarAlumnos(byFiltro: boolean) {
    this.anuncioService.getAllUsuariosFiltrados(this.filtros, this.page, this.pageSize).subscribe(result => {
      this.listadoAlumnos = result.content;
      if (byFiltro) {
        this.listadoPaginator.data = this.listadoAlumnos;
      }
      this.listadoPaginator.paginator = this.paginator;

    })
  }

  private _listarTipoAnuncio() {
    this.anuncioService.getAllTipoAnuncio().subscribe(result => {
      this.listadoTipoAnuncio = result;
    })
  }

  private _listarFiltros() {
    this.anuncioService.getAllFiltros().subscribe(result => {
      this.listadoTipoFiltro = result;
      this._initFiltros();
    })
  }

  //* CAMBIOS DE FILTROS */

  public cambiarTipoAlumno(tipoAlumno: string[]) {
    this.tipoAlumno = tipoAlumno;
    this._setFiltros();
    this._listarAlumnos(true);
  }

  public cambiarPrograma(programa: string) {
    let strPrograma = "";
    switch (programa) {
      case "PRE":
        let programasPregrado = [...this.listadoFiltroPrograma.filter(p => p.value.substring(0,2) == 'LC' || p.value.substring(0,2) == 'LI')];
        programasPregrado.forEach(p => {
          strPrograma = strPrograma + p.value + ",";
        })
        strPrograma = strPrograma.substring(0,strPrograma.length-1)
        break;
      case "POST":
        let programasPostgrado = [...this.listadoFiltroPrograma.filter(p => p.value.substring(0,2) != 'LC' && p.value.substring(0,2) != 'LI' && p.value != 'TODOS')];
        programasPostgrado.forEach(p => {
          strPrograma = strPrograma + p.value + ",";
        })
        strPrograma = strPrograma.substring(0,strPrograma.length-1)
        break;
      case "TODOS":
        strPrograma = programa;
      default:
        break;
    }
    this.programaCod = strPrograma;
    console.log(this.programaCod);

    this._setFiltros();
    this._listarAlumnos(true);
  }

  public cambiarStatus(status: string[]) {
    console.log(status);
    this._setFiltros();
    this._listarAlumnos(true);
  }

  public cambiarEscFac(escFac: string[]) {
    this.escFacCod = escFac;
    this._setFiltros();
    this._listarAlumnos(true);
  }

  public buscarIdBanner(idBanner: string) {
    this.idBanner = idBanner != "" ? idBanner : "TODOS";
    this._setFiltros();
    this._listarAlumnos(true);
  }

  public paginatorValues(e: PageEvent) {
    this.pageSize = e.pageSize;
    this.page = e.pageIndex + 1;
    this._listarAlumnos(false);
  }

  _isSeleccion(valor: number) {
    this.isSeleccion = valor == 1 ? false : true;
    this.tipoAnuncio = valor;
  }

  _pushNotification(valor: boolean) {
    this.isPushNotification = valor;
  }

  _programada(valor: boolean) {
    if (!valor) {
      this.formAnuncio.controls['fechaPublicacion'].setValue(null);
    } else {
      this.formAnuncio.controls['fechaPublicacion'].setErrors({ invalid: true });
    }
    // console.log(this.formAnuncio);
    this.isProgramada = valor;
  }

  //* LECTURA DE IMAGENES Y CONVERSION */

  changeListener(files: FileList) {
    if (files && files.length > 0) {
      let file: File = files.item(0);
      if (file.type == "image/png" || file.type == "image/jpeg") {
        this.fileToBase64(file).then(result => {
          this.imagenBase64 = result;
          console.log(result);
        });
      } else {
        this.utilAlert.toastError("NO ES IMAGEN");
      }
    }
  }

  private fileToBase64 = (file: File): Promise<string> => {
    return new Promise<string>((resolve, reject) => {
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => resolve(reader.result.toString());
      reader.onerror = error => reject(error);
    })
  }

  //* SERVICIOS PARA GUARDAR Y ENVIAR ANUNCIOS */

  public saveAnuncio(): void {
    this.blockUI.start("Enviando anuncio...");
    let enviarAnuncio = this.crearAnuncio();
    if (enviarAnuncio && this.formAnuncio.valid) {
      this.anuncioService.saveAnuncio(this.anuncioTemp).subscribe(result => {
        this.router.navigateByUrl('/app-terrae/anuncios');
        this.utilAlert.successSwal("Se ha enviado su anuncio N°" + result.id);
        this.blockUI.stop();
      }, (_error) => {
        this.blockUI.stop();
        this.utilAlert.toastError(_error?.error?.message)
      })
    } else {
      this.utilAlert.toastError("Ha ocurrido un error al momento de enviar el anuncio.");
      this.blockUI.stop();
    }
  }

  public buscarSeleccion(): void {
    this.blockUI.start("Enviando anuncio...");
    let enviarAnuncio = this.crearAnuncio();
    if (enviarAnuncio && this.formAnuncio.valid) {
      this.enviarConSeleccion = true;
      this._listarFiltros();
      this._setFiltros();
      this._listarAlumnos(true);
      this.blockUI.stop();
    } else {
      this.utilAlert.toastError("Ha ocurrido un error al momento de enviar el anuncio.");
      this.blockUI.stop();
    }
  }


  private crearAnuncio(): boolean {
    try {
      //* TIPO
      this.tipoAnuncioTemp = this.listadoTipoAnuncio.filter(t => t.id == this.tipoAnuncio)[0];

      //* CONFIG
      this.configAnuncio.pushNotification = this.isPushNotification;
      this.configAnuncio.programada = this.isProgramada;
      let fechaPublicacionString = moment(this.formAnuncio.controls['fechaPublicacion'].value).format('yyyy-MM-DD HH:mm:ss');
      this.configAnuncio.fechaPublicacion = fechaPublicacionString;

      //* ANUNCIO
      this.anuncioTemp.titulo = this.formAnuncio.controls['titulo'].value;
      this.anuncioTemp.mensaje = this.formAnuncio.controls['mensaje'].value;
      this.anuncioTemp.titulo = this.formAnuncio.controls['titulo'].value;
      this.anuncioTemp.link = this.formAnuncio.controls['link'].value;
      this.anuncioTemp.imagen = this.imagenBase64;
      this.anuncioTemp.configAnuncio = this.configAnuncio;
      this.anuncioTemp.tipoAnuncio = this.tipoAnuncioTemp;

      if (this.isSeleccion) {
        this.anuncioTemp.usuariosNotificar = this.listadoAlumnosFiltrado;
      }

      return true;
    } catch (error) {
      this.utilAlert.toastError("Ocurrio un error al momento de enviar el anuncio.");
      return false;
    }
  }

  //* SERVICIOS VARIADOS */

  public delayLink(value: string) {
    clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      this.validarLink(value)
    }, 500);
  }

  public addBreakLine(form: FormControl){
    let text = form.value;
    console.log(text);
    text = text.replace(/\./g, '\r\n');
    form.setValue(text);
  }

  public validarLink(value: string) {
    let url = new URL(value);
  }

  public volver() {
    if (this.enviarConSeleccion) {
      this.enviarConSeleccion = false;
    } else {
      this.router.navigateByUrl('/app-terrae/anuncios');
    }
  }

  //* SERVICIOS PARA AGREGAR Y QUITAR ALUMNOS */

  public agregarAlumno(item: UsuarioApp) {
    if (this.listadoAlumnosFiltrado.filter(a => a.id == item.id).length > 0) {
      this.utilAlert.toastError("El alumno ya se encuentra agregado.");
    } else {
      this.listadoAlumnosFiltrado.push(item);
      this.listadoFiltradoPaginator.data = this.listadoAlumnosFiltrado;
      this.listadoFiltradoPaginator.paginator = this.listadoAlumnosFiltrado.length > 2 ? this.paginatorFiltrado : undefined;
      // console.log(this.listadoFiltradoPaginator.data);
      // console.log(this.listadoFiltradoPaginator.paginator);
    }
  }

  public quitarAlumno(item: UsuarioApp) {
    // console.log(item);
    this.listadoAlumnosFiltrado = this.listadoAlumnosFiltrado.filter(a => a.id != item.id);
    this.listadoFiltradoPaginator.data = this.listadoAlumnosFiltrado;
  }

  public quitarTodo() {
    this.listadoAlumnosFiltrado = [];
    this.listadoFiltradoPaginator.data = this.listadoAlumnosFiltrado;
  }

}
