import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { IndexAnunciosComponent } from './index-anuncios.component';
import { IndexAnunciosNewComponent } from './index-anuncios-new/index-anuncios-new.component';

const routes: Routes = [
  { path: '', component: IndexAnunciosComponent },
  { path: 'new', component: IndexAnunciosNewComponent }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class IndexAnunciosRoutingModule { }
