import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { IPaginacion } from '../../interfaces/paginacion.interface';
import { SearchPagination } from '../../interfaces/search.pagination';
import { SolicitudModel } from '../../models/aerc-models/solicitud-model';
import { TipoSolicitudModel } from '../../models/aerc-models/tipo-solicitud-model';
import { AnuncioAppModel } from '../../models/app-terrae/anuncios/anuncio.model';
import { UsuarioModel } from '../../models/aerc-models/usuario-model';
import { FiltroAlumnoApp } from '../../interfaces/app-terrae/filtro-alumno.interface';
import { UsuarioApp } from '../../interfaces/app-terrae/usuario-app.interface';
import { TipoAnuncioAppModel } from '../../models/app-terrae/anuncios/tipo-anuncio.model';
import { FiltroAnuncioModel } from '../../models/app-terrae/anuncios/filtro-anuncio.model';

@Injectable({
  providedIn: 'root'
})
export class AnuncioAppService {


  private urlBase = `${environment.backend.app_terrae}api/anuncio`;
  private urlBaseFiltro = `${environment.backend.app_terrae}api/tipo-usuario`;

  constructor(
    private http: HttpClient,
  ) { }

  public getAllAnuncios(search: string, page: number, records: number, enviado: boolean): Observable<IPaginacion<AnuncioAppModel>> {
    return this.http.get<IPaginacion<AnuncioAppModel>>(`${this.urlBase}/creados?page=${page}&records=${records}&search=${search}&enviado=${enviado}`);
  }

  public getAllTipoAnuncio(): Observable<Array<TipoAnuncioAppModel>> {
    return this.http.get<Array<TipoAnuncioAppModel>>(`${this.urlBase}/tipos`);
  }

  public getAllFiltros(): Observable<Array<FiltroAnuncioModel>> {
    return this.http.get<Array<FiltroAnuncioModel>>(`${this.urlBaseFiltro}/filtros`);
  }

  public getAllUsuariosFiltrados(filtros: FiltroAlumnoApp, page: number, records: number): Observable<IPaginacion<UsuarioApp>> {
    return this.http.post<IPaginacion<UsuarioApp>>(`${this.urlBaseFiltro}/all?page=${page}&records=${records}`, filtros);
  }

  public saveAnuncio(anuncio: AnuncioAppModel): Observable<AnuncioAppModel> {
    return this.http.post<AnuncioAppModel>(`${this.urlBase}`, anuncio);
  }

}
