import { Component, OnInit } from '@angular/core';
import { AnuncioAppService } from '../../../commons/services/app-terrae-services/anuncio.service';
import { AnuncioAppModel } from '../../../commons/models/app-terrae/anuncios/anuncio.model';import { FormControl, FormGroup } from '@angular/forms';
import { listarSolicitudes } from '../../../store/actions/aerc-actions/solicitud.actions';
import { TipoAnuncioAppModel } from 'src/app/commons/models/app-terrae/anuncios/tipo-anuncio.model';

@Component({
  selector: 'app-index-anuncios',
  templateUrl: './index-anuncios.component.html',
  styles: [
  ]
})
export class IndexAnunciosComponent implements OnInit {

  public listadoAnuncios: AnuncioAppModel[] = [];
  public listadoTipoAnuncio: TipoAnuncioAppModel[] = [];

  public formTopBar: FormGroup;
  public tipo: number = 0;
  public pageSize: number = 5;
  public page: number = 1;
  public enviado: boolean = true;
  public totalElements: number = 0;

  constructor(
    public anuncioService: AnuncioAppService
  ) { }

  ngOnInit(): void {
    this._listarAnuncios();
    this._listarTipoAnuncio();
    this.crearTopBar();
  }

  crearTopBar() {
    this.formTopBar = new FormGroup({
      tipo: new FormControl(""),
      registros: new FormControl(""),
      enviado: new FormControl(""),
    });

    this.formTopBar.get("tipo").setValue(this.tipo.toString());
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
    this.formTopBar.get("enviado").setValue(this.enviado);
  }

  _listarAnuncios() {
    this.anuncioService.getAllAnuncios("", this.page, this.pageSize, this.enviado).subscribe(result => {
      this.listadoAnuncios = result.content;
      this.totalElements = result.totalElements;
    })
  }

  private _listarTipoAnuncio() {
    this.anuncioService.getAllTipoAnuncio().subscribe(result => {
      this.listadoTipoAnuncio = result;
    })
  }

  public cambiarEstado(estado: boolean) {
    this.enviado = estado;
    this._listarAnuncios();
  }

  public cambiarTipo(tipoAnuncio: number) {
    this.tipo = tipoAnuncio;
    this._listarAnuncios();
  }

  public cambiarRegistros(cant: number) {
    this.pageSize = cant;
    this._listarAnuncios();
  }
}
